//  detect

const OMZepto = window.OMZepto

const ua = navigator.userAgent

OMZepto.webview = {};

(() => {
// 
  let weixin = ua.match(/MicroMessenger\/((\d+)\.(\d+))/i)
  if (weixin) {
    OMZepto.webview.weixin = true
    OMZepto.webview.version = parseFloat(weixin[1])
    return
  }

  // Q
  let mqq = ua.match(/ QQ[\s\/]([\d\.]*)/i)
  if (mqq) {
    OMZepto.webview.mqq = true
    OMZepto.webview.version = parseFloat(mqq[1])
    return
  }

  // 
  let qqlive = ua.match(/qqlivebrowser\/([^\s]+)/i)
  if (qqlive) {
    OMZepto.webview.qqlive = true
    OMZepto.webview.version = parseFloat(qqlive[1])
    return
  }

  // QQ 
  let qqbrowser = ua.match(/ MQQBrowser[\s\/]([\d\.]*)/i)
  if (qqbrowser) {
    OMZepto.webview.qqbrowser = true
    OMZepto.webview.version = parseFloat(qqbrowser[1])
    return
  }

  // 
  let qqnews = ua.match(/qqnews[\s\/]([\d\.]*)/i)
  if (qqnews) {
    OMZepto.webview.qqnews = true
    OMZepto.webview.version = parseFloat(qqnews[1])
    return
  }

  // 
  let samsungbrowser = ua.match(/SamsungBrowser[\s\/]([\d\.]*)/i)
  if (samsungbrowser) {
    OMZepto.webview.samsungbrowser = true
    OMZepto.webview.version = parseFloat(samsungbrowser[1])
    return
  }

  // 
  let xiaomibrowser = ua.match(/HM|RedMi|Mi/ig)
  if (xiaomibrowser) {
    OMZepto.webview.xiaomibrowser = true
    return
  }

  // 
  let huaweibrowser = ua.match(/huawei|honor/ig)
  if (huaweibrowser) {
    OMZepto.webview.huaweibrowser = true
    return
  }

  // vivo
  let vivobrowser = ua.match(/vivo/ig)
  if (vivobrowser) {
    OMZepto.webview.vivobrowser = true
    return
  }

})()

